/* App.css */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: "Segoe UI", Roboto, sans-serif;
  background: url('https://images.unsplash.com/photo-1557683316-973673baf926') no-repeat center center fixed;
  background-size: cover;
}

.app-container {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.background-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 0;
}

.upload-box {
  position: relative;
  z-index: 1;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  border-radius: 16px;
  text-align: center;
}

.glass-effect {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 32px rgba(0,0,0,0.2);
}

.title {
  font-size: 26px;
  margin-bottom: 10px;
  font-weight: bold;
  color: #fff;
}

.subtitle {
  font-size: 14px;
  color: #e0e0e0;
  margin-bottom: 30px;
}

.file-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 2px dashed #ccc;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.4);
  color: #333;
  cursor: pointer;
}

.file-input:hover {
  border-color: #90cdf4;
}

.upload-button {
  width: 100%;
  padding: 12px;
  font-weight: bold;
  border: none;
  background: linear-gradient(to right, #667eea, #764ba2);
  color: white;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s;
}

.upload-button:hover {
  background: linear-gradient(to right, #5a67d8, #6b46c1);
}

.download-section {
  margin-top: 20px;
  background: rgba(240, 253, 244, 0.6);
  padding: 10px;
  border: 1px solid #bbf7d0;
  border-radius: 8px;
  color: #065f46;
}

.download-text {
  font-size: 14px;
  margin-bottom: 5px;
}

.download-link {
  color: #047857;
  word-break: break-all;
  text-decoration: underline;
  font-weight: 500;
}